import React from "react";
import SidebarSingleItem from "./sidebarSingleItem";

const SideBar = () => {
  return (
    <nav
      id="sidebarMenu"
      className="col-md-3 col-lg-2 d-md-block sidebar collapse"
    >
      <div className="sidebar-sticky pt-5">
        <h6 className="sidebar-heading d-flex justify-content-between align-items-center px-5 mt-4 mb-2 text-muted">
          <span className="pl-2 font-weight-bold">MENU</span>
        </h6>
        <ul className="nav flex-column ">
          <SidebarSingleItem
            label="Explore"
            icon="fas fa-arrow-alt-circle-down"
            active
          />
          <SidebarSingleItem label="Genres" icon="fas fa-volume-down" />
          <SidebarSingleItem label="Albums" icon="fas fa-compact-disc" />
          <SidebarSingleItem label="Artists" icon="fas fa-microphone" />
          <SidebarSingleItem label="Radio" icon="fas fa-podcast" />
        </ul>
        <h6 className="sidebar-heading d-flex justify-content-between align-items-center px-5 mt-4 mb-2 text-muted">
          <span className="pl-2 font-weight-bold">LIBRARY</span>
        </h6>
        <ul className="nav flex-column ">
          <SidebarSingleItem label="Recent" icon="fas fa-history" />
          <SidebarSingleItem label="Albums" icon="fas fa-compact-disc" />
          <SidebarSingleItem label="Favourites" icon="fas fa-heart" />
          <SidebarSingleItem label="Local" icon="fas fa-folder" />
        </ul>
        <h6 className="sidebar-heading d-flex justify-content-between align-items-center px-5 mt-4 mb-2 text-muted">
          <span className="pl-2 font-weight-bold">PLAYLIST</span>
        </h6>
        <ul className="nav flex-column ">
          <SidebarSingleItem label="Create New" icon="fas fa-plus-square" />
          <SidebarSingleItem label="Design Flow" icon="fas fa-play-circle" />
          <SidebarSingleItem label="Best of 2020" icon="fas fa-play-circle" />
          <SidebarSingleItem label="Nigeria James" icon="fas fa-play-circle" />
        </ul>
      </div>
    </nav>
  );
};

export default SideBar;
