import React, { Component } from "react";

class TableBody extends Component {
  render() {
    const { columns } = this.props;

    return (
      <tbody>
        {columns.map((col) => (
          <tr className="d-flex">
            {col.map((c) => (
              <th
                key={c.key}
                className={
                  c.key === "song"
                    ? "col align-self-center"
                    : "align-self-center"
                }
              >
                {c.content}
              </th>
            ))}
          </tr>
        ))}
      </tbody>
    );
  }
}

export default TableBody;
