import React from "react";
import { Link } from "react-router-dom";
const SidebarSingleItem = (props) => {
  const { label, icon, active } = props;
  return (
    <li className="pl-5 nav-item my-1">
      <Link className={active ? "nav-link active" : "nav-link"} href="#">
        <span>
          <i className={icon + " custom-small pr-3"}></i>
        </span>
        <span className="pl-3 font-weight-bold">{label}</span>
      </Link>
    </li>
  );
};

export default SidebarSingleItem;
