import React from "react";
import { Link } from "react-router-dom";
import profPic from "../profile.jpg";

const NavBar = () => {
  return (
    <nav className="navbar navbar-expand sticky-top flex-md-nowrap p-0">
      <span className="navbar-brand col-md-3 col-lg-2 pl-5 py-3 font-weight-bold">
        <Link href="#">
          <i className="fas fa-music pr-3 pt-3"></i>
          Groovvy
        </Link>
      </span>
      <div
        className="collapse navbar-collapse  pt-3 "
        id="navbarSupportedContent"
      >
        <ul className="navbar-nav pl-3">
          <li className="nav-item mx-1 active">
            <Link className="nav-link" href="#">
              Music
            </Link>
          </li>
          <li className="nav-item mx-1">
            <Link className="nav-link" href="#">
              Podcast
            </Link>
          </li>
          <li className="nav-item mx-1">
            <Link className="nav-link" href="#">
              Live
            </Link>
          </li>
        </ul>
        <div className="input-group col-3 search-input mx-auto">
          <div className="input-group-prepend">
            <button className="btn btn-outline-secondary" type="button">
              <i className="fas fa-search px-1"></i>
            </button>
          </div>
          <input
            type="text"
            className="form-control pl-1 py-4"
            placeholder="Type here to search"
          />
        </div>
        <ul className="navbar-nav ml-auto pr-3 mr-5">
          <li className="nav-item mx-1">
            <Link className="nav-link" href="#">
              <i className="fas fa-bell">
                <span className="badge badge-primary"> </span>
              </i>
            </Link>
          </li>
          <li className="nav-item mx-1">
            <Link className="nav-link" href="#">
              <i className="fas fa-cog"></i>
            </Link>
          </li>
          <li className="nav-item mx-1">
            <Link
              className="nav-link profilePic pb-0 pl-0 pr-3 pt-0 text-light"
              href="#"
            >
              <img src={profPic} className="p-2 mr-2 img-fluid" alt="profPic" />
              <span className="p-3">Bob Morley</span>
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default NavBar;
