import React from "react";
import { Link } from "react-router-dom";
const Artist = (props) => {
  const { img, name, plays } = props;
  return (
    <div className="col-2 text-center">
      <Link to="#">
        <img src={img} alt="" height="70px" />
        <p className="mt-2 mb-n1 font-weight-bold small">{name}</p>
        <small className="mt-n1 font-weight-bold text-muted">
          {plays} Plays
        </small>
      </Link>
    </div>
  );
};

export default Artist;
