import React from "react";
import { Link } from "react-router-dom";
import Artist from "./artist";
import Billie from "../images/billie.jpg";
import Travis from "../images/travis.jpg";
import Weeknd from "../images/weeknd.jpg";
import GaGa from "../images/lady gaga.jpg";
import Eminem from "../images/eminem.jpg";
import ImagineDragons from "../images/Imagine Dragons.jpg";
const TopArtists = () => {
  return (
    <div className="card top-artists">
      <div className="card-header d-flex justify-content-between">
        <h6 className="font-weight-bold">Top Artists</h6>
        <Link to="#" className="small text-muted font-weight-bold">
          See all
        </Link>
      </div>
      <div className="card-body row">
        <Artist name="Billie Eilish" plays="203M" img={Billie} />
        <Artist name="Travis Scott" plays="44M" img={Travis} />
        <Artist name="The Weeknd" plays="63M" img={Weeknd} />
        <Artist name="Lady Gaga" plays="180M" img={GaGa} />
        <Artist name="Eminem" plays="100M" img={Eminem} />
        <Artist name="Imagine Dragons" plays="70M" img={ImagineDragons} />
      </div>
    </div>
  );
};

export default TopArtists;
