import React from "react";
import img from "../images/camila.png";
const Carousel = () => {
  return (
    <div
      id="carouselExampleCaptions"
      className="carousel slide carousel-fade"
      data-ride="carousel"
    >
      <span className="carousel-title font-weight-bold text-muted">
        Trending New Hits
      </span>
      <ol className="carousel-indicators">
        <li
          data-target="#carouselExampleCaptions"
          data-slide-to="0"
          className="active"
        ></li>
        <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
        <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
      </ol>
      <div className="carousel-inner">
        <div className="carousel-item active">
          <img src={img} className="d-block float-right" alt="cover" />
          <div className="carousel-caption d-block">
            <h1 className="font-weight-bold carousel-caption-heading">
              In My Feelings
            </h1>
            <p className="text-white font-weight-bold">
              Camila Cabello
              <span className="text-muted pl-3">63Million Plays</span>
            </p>
            <div className="d-flex align-items-center mt-4">
              <button className="btn btn-primary">Listen Now</button>
              <i className="fas fa-heart ml-3"></i>
            </div>
          </div>
        </div>
        <div className="carousel-item">
          <img src={img} className="d-block float-right" alt="cover" />
          <div className="carousel-caption d-block">
            <h1 className="font-weight-bold carousel-caption-heading">
              In My Feelings
            </h1>
            <p className="text-white font-weight-bold">
              Camila Cabello
              <span className="text-muted pl-3">63Million Plays</span>
            </p>
            <div className="d-flex align-items-center mt-4">
              <button className="btn btn-primary">Listen Now</button>
              <i className="fas fa-heart ml-3"></i>
            </div>
          </div>
        </div>
        <div className="carousel-item">
          <img src={img} className="d-block float-right" alt="cover" />
          <div className="carousel-caption d-block">
            <h1 className="font-weight-bold carousel-caption-heading">
              In My Feelings
            </h1>
            <p className="text-white font-weight-bold">
              Camila Cabello
              <span className="text-muted pl-3">63Million Plays</span>
            </p>
            <div className="d-flex align-items-center mt-4">
              <button className="btn btn-primary">Listen Now</button>
              <i className="fas fa-heart ml-3"></i>
            </div>
          </div>
        </div>
      </div>
      <a
        className="carousel-control-prev"
        href="#carouselExampleCaptions"
        role="button"
        data-slide="prev"
      >
        <i className="fas fa-chevron-up"></i>
        <span className="sr-only">Previous</span>
      </a>
      <a
        className="carousel-control-next"
        href="#carouselExampleCaptions"
        role="button"
        data-slide="next"
      >
        <i className="fas fa-chevron-down"></i>
        <span className="sr-only">Next</span>
      </a>
    </div>
  );
};

export default Carousel;
