import React from "react";
import { Link } from "react-router-dom";
import TopArtists from "./topArtists";
import ArtistTable from "./artistTable";
import Player from "./player";
import PlayerButtons from "./playerButtons";
import Carousel from "./carousel";
import Genres from "./genres";

const Main = () => {
  return (
    <main role="main" className="col-md-9 ml-sm-auto col-lg-10 px-md-4">
      <div className="current-playing">
        <Carousel />
      </div>
      <div className="col-12 row">
        <div className="col-8 row">
          <div className="col-12 mb-3">
            <TopArtists />
          </div>
          <div className="col-5">
            <div className="card">
              <div className="card-header d-flex justify-content-between">
                <h6 className="font-weight-bold">Genres</h6>
                <Link to="#" className="small text-muted font-weight-bold">
                  See all
                </Link>
              </div>
              <div className="card-body genres-col py-0">
                <Genres />
              </div>
            </div>
          </div>
          <div className="col-7">
            <div className="card">
              <div className="card-header d-flex justify-content-between">
                <h6 className="font-weight-bold">Top Artists</h6>
                <Link to="#" className="small text-muted font-weight-bold">
                  See all
                </Link>
              </div>
              <div className="card-body py-0">
                <ArtistTable />
              </div>
            </div>
          </div>
        </div>
        <div className="col-4">
          <div className="card player">
            <div className="card-header d-flex justify-content-between">
              <h6 className="font-weight-bold">Player</h6>
              <Link to="#" className="small text-muted font-weight-bold">
                <i className="fas fa-music"></i>
              </Link>
            </div>
            <div className="card-body text-center">
              <Player />
            </div>
            <div className="card-footer">
              <PlayerButtons />
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Main;
