import React from "react";
import Cover from "../images/table-covers/torn.png";
const Player = () => {
  return (
    <React.Fragment>
      <img src={Cover} alt="cover" className="cover" />
      <h3 className="pt-2">Torn</h3>
      <h6 className="mb-0">Ava Max</h6>
      <p className="text-muted">Teen Beats</p>
      <div className="col row p-0 m-0 my-3">
        <div className="col-2 small">
          <span className="text-nowrap">2:58</span>
        </div>
        <div className="col-8 d-flex align-items-center ">
          <span className="passed"></span>
          <span className="notPassed"></span>
          <span className="circle"></span>
        </div>
        <div className="col-2 small">
          <span className="text-nowrap text-muted">3:45</span>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Player;
