import React from "react";
import { Link } from "react-router-dom";

const PlayerButtons = () => {
  return (
    <React.Fragment>
      <ul className="nav justify-content-center mt-4">
        <li className="nav-item d-flex align-self-center">
          <Link className="nav-link" href="#">
            <i className="fas fa-undo text-white"></i>
          </Link>
        </li>
        <li className="nav-item d-flex align-self-center">
          <Link className="nav-link" href="#">
            <i className="fas fa-step-backward text-white"></i>
          </Link>
        </li>
        <li className="nav-item d-flex align-self-center">
          <Link className="nav-link" href="#">
            <i className="fas fa-play btn-play"></i>
          </Link>
        </li>
        <li className="nav-item d-flex align-self-center">
          <Link className="nav-link" href="#">
            <i className="fas fa-step-forward text-white"></i>
          </Link>
        </li>
        <li className="nav-item d-flex align-self-center">
          <Link className="nav-link" href="#">
            <i className="fas fa-random text-white "></i>
          </Link>
        </li>
      </ul>
      <p className="font-weight-bold small lyrics text-center mt-4 mb-0">
        LYRICS
      </p>
    </React.Fragment>
  );
};

export default PlayerButtons;
